<template>
  <!--:close-on-click-modal="false == true"-->
    <div class="accompany-wrapper">
      <el-dialog
        title="/"
        :visible.sync="onlyVisible"
        class="d-wrapper"
        >
        <div class="content-wrapper">
          <div class="line"></div>
          <div class="split"></div>
          <div class="base-info">
            <div class="avatar">avatar</div>
            <div class="info">
              <div class="number">
                <span class="txt">: </span><span class="index">001</span>
                <span class="txt">ID: </span><span class="id">254561</span>
              </div>
              <div class="nick-name">
                <span class="txt">:</span>
                <input type="text" class="name" readonly="readonly" v-model="name"/>
              </div>
              <div class="other">
                <div class="sex">
                  <span class="txt">:</span>
                  <div class="sex-radio">
                    <label><input type="radio" name="sex"/> </label>
                    <label><input type="radio" name="sex"/> </label>
                  </div>
                </div>
                <div class="extend">
                  <span class="txt">:</span>
                  <input type="text" class="extend-input">
                  <my-button :commonSrc="extendSrc" class="btn-extend"></my-button>
                </div>
              </div>
            </div>
          </div>
          <div class="split"></div>
          <div class="select-wrapper">
            <div class="title"><span>1:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
            <div class="title"><span>1:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>2:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
            <div class="title"><span>2:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>3:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
            <div class="title"><span>3:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>:</span></div>
            <el-time-picker
              arrow-control
              v-model="timeOnline"
              :picker-options="{
                selectableRange: '18:30:00 - 20:30:00'
              }"
              class="timeSelect"
              placeholder="">
            </el-time-picker>
            <div class="title"><span>:</span></div>
            <el-time-picker
              arrow-control
              v-model="timeOffline"
              :picker-options="{
                selectableRange: '18:30:00 - 20:30:00'
              }"
              class="timeSelect"
              placeholder="">
            </el-time-picker>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>ID:</span></div>
            <input type="text" class="club-id" v-model="clubId">
          </div>
          <div class="select-wrapper">
            <div class="title"><span>:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <div class="btn-close">
            <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="_close"></my-button>
            <my-button :commonSrc="closeSrc" class="close-btn" eventName="close-event" @close-event="_close"></my-button>
          </div>
        </div>


      </el-dialog>
    </div>
</template>

<script>
  import MyButton from "../myButton/MyButton.vue";
  export default {
    data() {
      return {
        onlyVisible: this.dialogVisible,
        name: "",
        extendSrc: "btn_extend",
        timeOnline: null,
        timeOffline: null,
        clubId: 123129,
        agreeSrc: "btn_agree",
        closeSrc: "btn_close"
      }
    },
    methods: {
      handleClose(done) {
            done();
      },
      _close() {
        console.log("");
        this.$emit("closeDialog");
      },
      closeDialog() {
        this.onlyVisible = false;
      },
      openDialog() {
        this.onlyVisible = true;
      }
    },
    props: {
      dialogVisible: {
        type: Boolean,
        default: false
      }
    },
    components: {
      'my-button': MyButton
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  @import "./stylus/accompony.styl";
</style>



