<template>
    <!--:close-on-click-modal="false == true"-->
    <div class="modify-wrapper">
      <el-dialog
        title=""
        :visible.sync="onlyVisible"
        class="m-wrapper"

      >
        <div class="content-wrapper">
          <div class="line"></div>
          <div class="split" style="margin-top: 23px;"></div>
          <div class="game">
            <div class="title"><span>1:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
            <div class="title"><span>2:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
            <div class="title"><span>3:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <div class="split" style="margin-top: 23px;"></div>
          <div class="select-wrapper">
            <div class="title"><span>1:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <div class="split" style="margin-top: 23px;"></div>
          <div class="select-wrapper">
            <div class="title"><span>:</span></div>
            <el-time-picker
              arrow-control
              v-model="timeOnline"
              :picker-options="{
                selectableRange: '18:30:00 - 20:30:00'
              }"
              class="timeSelect"
              placeholder="">
            </el-time-picker>
            <div class="title"><span>:</span></div>
            <el-time-picker
              arrow-control
              v-model="timeOffline"
              :picker-options="{
                selectableRange: '18:30:00 - 20:30:00'
              }"
              class="timeSelect"
              placeholder="">
            </el-time-picker>
          </div>
          <div class="split" style="margin-top: 23px;"></div>
          <div class="select-wrapper">
            <div class="title"><span>:</span></div>
            <select>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <div class="btn-close">
            <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="_close"></my-button>
            <my-button :commonSrc="closeSrc" class="close-btn" eventName="close-event" @close-event="_close"></my-button>
          </div>
        </div>
      </el-dialog>
    </div>
</template>

<script>
  import MyButton from "../myButton/MyButton.vue";
  export default {
    data() {
      return {
        onlyVisible: this.modifyDialog,
        name: "",
        extendSrc: "btn_extend",
        timeOnline: null,
        timeOffline: null,
        clubId: 123129,
        agreeSrc: "btn_agree",
        closeSrc: "btn_close"
      }
    },
    methods: {
      handleClose(done) {
        done();
      },
      _close() {
        console.log("");
        this.$emit("closeModifyDialog");
      },
      closeDialog() {
        this.onlyVisible = false;
      },
      openDialog() {
        this.onlyVisible = true;
      }
    },
    props: {
      modifyDialog: {
        type: Boolean,
        default: false
      }
    },
    components: {
      'my-button': MyButton
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  @import "./stylus/modify.styl";
</style>



