<template>
    <div class="wrapper">
      <div class="header">
        <img src="./resource/login_logo.png" >
      </div>
      <div class="box">
        <!--<el-button type="primary" @click="_login"></el-button>-->
        <!--<el-button type="primary" style="margin-left: 10px" @click="_register"></el-button>-->
        <!--<el-button type="primary" style="margin-left: 10px" @click="_forgetPwd"></el-button>-->
        <!---->


        <el-input placeholder="" v-model="username" clearable class="name-input"></el-input>
        <div class="pwd-div">
          <el-input placeholder="" v-model="password" show-password class="pwd-input"></el-input>
          <!---->
          <el-button type="text" class="btn-forget"></el-button>
        </div>

        <!--<span class="forget"></span>-->
        <div class="checkcha-div">
          <input type="text" class="check-input" placeholder="" v-model="inputCheckcha">
          <canvas class="checkcha" width="115" height="50" id="auth-code" @click="_changeCheck">{{checkcha}}</canvas>
        </div>
        <el-button type="success" class="btn-login" @click="_login"></el-button>

      </div>
      <div class="footer">
        <span>Copyright  Foxuc</span>
        <br/>
        <span>Powered by Vue.js</span>
      </div>
    </div>
</template>

<script>

  import writeAuthCode from '../../utils/checkCha/checkCha.js'
  export default {
    data() {
      return {
        username: '',
        password: '',
        checkcha: "",
        inputCheckcha: ''
      }
    },
    created() {

    },
    mounted() {
      //dom
      this.$nextTick(() => {
        this._getCheckcha();
      })
    },
    methods: {
      _login() {
        if (this.inputCheckcha.toString() !== this.checkcha) {
          this.$alert('', '', {
            confirmButtonText: '',
//            callback: action => {
//              this.$message({
//                type: 'info',
//                message: `action: ${ action }`
//              });
//            }
          }).catch(err => {
            console.log(err);
          });
          this.inputCheckcha = '';
          console.log("!");
          return ;
        }
        console.log("");
        console.log(this.$router.push({path: 'main/mainControl'}));
      },
      _register() {

      },
      _forgetPwd() {

      },
      _getCheckcha() {
        this.checkcha = this._getRandomCheck();
        let options = {
          canvasId: "auth-code",/**canvasid*/
          txt: this.checkcha,/***/
          height: 50,/** */
          width: 115/** */
        };
        writeAuthCode(options);
      },
      _getRandomCheck() {
        return Math.random().toString().substring(2,6);
      },
      _changeCheck() {
        this._getCheckcha();
      }
    }
  }
</script>
<style lang="stylus" type="text/stylus" scoped>
  @import './stylus/login.styl';
</style>



